   .projects-page {
      padding: 17.1rem 0 1rem;

      @media (max-width: $tablet) {
        padding: 10rem 0 1rem;
      }

      .title {
        margin: 0;
      }

      &__list {
         display: flex;
         flex-direction: column;
         gap: 19.3rem;
         margin-top: 12.5rem;
         margin-bottom: 28.9rem;

         @media (max-width: $mobile) {
            margin-top: 9.2rem;
            margin-bottom: 15rem;
            gap: 8.8rem;
         }

      }

      &__item {
        display: flex;
        align-items: flex-start;
        gap: 4rem;

        &--right {
          flex-direction: row-reverse;
          text-align: right;

          @media (max-width: $tablet) {
            text-align: left;
          }

          .swiper-nav {
            top: 0;
            right: auto;
            left: -15rem;
          }

          .projects__location {
            justify-content: flex-end;

            @media (max-width: $tablet) {
              justify-content: flex-start;
            }
          }
        }

        @media (max-width: $tablet) {
          flex-direction: column;
          align-items: flex-start;
        }

        @media (max-width: $mobile) {
          gap: 0;
        }
      }

      &__content {
         width: 100%;
         display: flex;
         flex-direction: column;
         min-height: 34.6rem;
         justify-content: space-between;

         @media (max-width: $tablet) {
            display: flex;
            flex-direction: column;
            text-align: left;
            margin-top: -9rem;
            width: 100%;
            height: auto;
            align-self: flex-end;
        }

        @media (max-width: $mobile) {
            margin-top: -3.3rem;
        }
      }

      &__slider {
        position: relative;
        width: 56rem;

        @media (max-width: $tablet) {
          width: 100%;
        }

        @media (max-width: $mobile) {
          margin-top: 0;
        }
      }

      .swiper-nav {
        position: absolute;
        top: 0;
        right: -15rem;

        @media (max-width: $tablet) {
          position: static;
          margin-top: 3rem;
        }

        @media (max-width: $mobile) {
          margin-top: 2rem;
        }
      }

      &__slide {
        width: 56rem;
        height: 34.6rem;

        img {
          object-fit: cover;
          width: 100%;
          height: 100%;
        }

        @media (max-width: $tablet) {
          height: 24.1rem;
        }
      }

      &__content-title {
        font-family: $bower;
        text-transform: uppercase;
        font-size: 1.8rem;
        line-height: 1.1;
        text-align: left;
         margin-left: 15rem;

         &--left {
            margin-left: 0rem;
            margin-right: 19rem;
         }

         @media (max-width: $tablet) {
              align-self: flex-end;
              margin: 0;
          }

        @media (max-width: $mobile) {
            font-size: 1.2rem;
            line-height: 1.1;
            max-width: 16.4rem;
        }

      }

      &__location {
        display: flex;
        align-items: center;
        gap: 1.2rem;
        font-size: 1.4rem;
        font-weight: 400;
        line-height: 1.2;
        margin: 0 0 1.7rem;

        @media (max-width: $mobile) {
          font-size: 1.2rem;
          margin: 0 0 1.3rem;
        }
      }

      &__text {
        margin: 0;
        font-size: 1.4rem;
        font-weight: 400;
        line-height: 1.2;
        text-align: left;

        @media (max-width: $mobile) {
          font-size: 1.2rem;
          line-height: 1.2;
        }
      }

      &__content-footer {
         display: flex;
         flex-direction: column;
         justify-content: flex-start;
         margin-top: 5rem;

         @media (max-width: $tablet) {
            margin-top: 6.5rem;
           }

         @media (max-width: $mobile) {
           margin-top: 3.5rem;
          }
      }

      .button {
         align-self: flex-start;
         margin-top: 2.4rem;
      }

    }


